import moment from 'moment-timezone'

import { GetNext, Interval } from '../scheduling.types'

/**
 * Generates next occurrence interval for non repeat strategy
 */
export function getNextNoRepeat({
  start,
  end,
  timezone,
}: GetNext): Interval | void {
  const nextOccurrenceStart = moment.tz(start, timezone).valueOf()
  const nextOccurrenceEnd = moment.tz(end, timezone).valueOf() - 1

  if (nextOccurrenceEnd <= nextOccurrenceStart) return

  return [nextOccurrenceStart, nextOccurrenceEnd]
}
